import { Component, h, Prop, Event, EventEmitter } from '@stencil/core';
import { EnumDocumentStatuses } from '../../models/EnumDocumentStatuses';
import { IDocument } from '../../types/Document';
import moment from 'moment';

@Component({
    tag: 'document-list-item',
    shadow: true,
    styleUrl: 'document-list-item.scss',
})
export class DocumentListItem {
    @Prop() public document: IDocument;
    @Event() openDocument: EventEmitter<IDocument>;

    constructor() {
        this.handleOpenDocument = this.handleOpenDocument.bind(this);
    }

    public render() {
        const documentIcon =
            this.document.status.toLowerCase() + ' document-icon';

        return (
            <li class="document-list-item" onClick={this.handleOpenDocument}>
                <div class={documentIcon}>
                    <limel-icon
                        name={this.getDocumentIcon(this.document.status)}
                        size="small"
                    />
                </div>
                <div class="document-info-container">
                    <div class="document-name">{this.document.name}</div>
                    <div class="document-status">
                        <span>{this.document.status}</span>
                        <span class="document-created-date">
                            {moment(this.document.created_at).format(
                                'YYYY-MM-DD'
                            )}
                        </span>
                    </div>
                </div>
            </li>
        );
    }

    private handleOpenDocument() {
        this.openDocument.emit(this.document);
    }

    private getDocumentIcon(status): string {
        switch (status) {
            case EnumDocumentStatuses.draft:
                return 'no_edit';
            case EnumDocumentStatuses.hardbounced:
                return 'error';
            case EnumDocumentStatuses.importing:
                return 'import';
            case EnumDocumentStatuses.lost:
                return 'drama';
            case EnumDocumentStatuses.processing:
                return 'submit_progress';
            case EnumDocumentStatuses.recalled:
                return 'double_left';
            case EnumDocumentStatuses.rejected:
                return 'private';
            case EnumDocumentStatuses.reviewed:
                return 'preview_pane';
            case EnumDocumentStatuses.scheduled:
                return 'overtime';
            case EnumDocumentStatuses.sealed:
                return 'lock';
            case EnumDocumentStatuses.sent:
                return 'wedding_gift';
            case EnumDocumentStatuses.signed:
                return 'autograph';
            case EnumDocumentStatuses.signedwithoutverification:
                return 'autograph';
            case EnumDocumentStatuses.viewed:
                return 'visible';
            default:
                return 'dancing_party';
        }
    }
}
